const isDarkMode = () => {
  // 
  const getSystemTheme = () => window.matchMedia("(prefers-color-scheme: dark)"); // {matches: boolean}
  // 
  const getLocalStorageTheme = () => window.localStorage.getItem('system-theme'); // dark | light
  // HTML data-theme 
  const getHtmlAttributeTheme = () => document.documentElement.getAttribute('data-theme'); // dark | light

  //  HTML >  > 
  let theme = getHtmlAttributeTheme();
  if (theme === undefined) {
    theme = getLocalStorageTheme();
  }
  const isDark = theme === 'dark' || getSystemTheme().matches;
  return isDark;
};

export default isDarkMode;
